import React from 'react'
import { I18n } from 'react-i18nify-lite'
import { Group } from 'prey-stash'
import { Dialog, Loading } from '../assets/Dialog'

const initMail = () => {
  if(window.Android){
    return window.Android.initMail();
  }else{
    return 'oso@oso.cl'
  } 
}

class EmailSent extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      data: {},
      isEmailSent: true, 
      isLoading: false,
      mail: initMail(),
    }
  }

  errorsFor = (name)=>{
    const errors = this.state.data[name]
    if(!errors) return
    if(errors.length === 0) return
    return <span ref='error' className="msg info-error">{
      errors.map((o)=>(
          <span>{o}</span>
        )
      )}
      </span>
  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  handleClick = (e)=>{
    this.setState({
      data: {} ,
      isLoading: true
      }, ()=>{setTimeout(this.callHandleClick, 0) 
    })
  }

  callHandleClick = (e)=>{
     var name=document.getElementById('email').value;
     if(window.Android){
       let json=window.Android.changemail(''+name);
       if(json!==''){
          this.setState({
            data: JSON.parse(json),
            isLoading: false
          })
       }else{
          this.setState({
            isEmailSent: true,
            isLoading: false,
            mail: name
          })
       }
     }else{
       let json='{"email":["no puede estar en blanco","es demasiado corto (el mnimo es de 6 caracteres)","debera ser un email"]}';
     //let json='';
      if(json!==''){
          this.setState({
            data: JSON.parse(json),
            isLoading: false
          })
      }else{
          this.setState({
            isEmailSent: true,
            isLoading: false,
            mail: name
          })
      }
     }/*
     */
  }

  closeEmailSent = (e) => {
    e.preventDefault()
    this.setState({
      isEmailSent: false,
    })
  }

  render() {
    return (
      <div className="fs-onboarding regular inverted">
      	<div className="column">
           <div className="sign-form">
           <h4 className="heading">{I18n.t(`email_validation.sent.title`)}</h4>
          <figure className="bg-fig">
            <i className="icon-email" />
          </figure>
          <h5>{I18n.t(`email_validation.sent.head_a`)}{this.state.mail}{I18n.t(`email_validation.sent.head_b`)}</h5>
           <h5>{I18n.t(`email_validation.sent.subtitle`)}</h5>
                <form autoComplete="off" className="form form-session" id="new-session"acceptCharset="UTF-8">
                       <ul className="form vvv">
                              <li>
                                <label>{I18n.t(`email_validation.sent.mail`)}</label>
                                <input id="email" name="email" required type="email" tabIndex="2" onFocus={this.handleFocus}   />
                                {this.errorsFor("email")}
                              </li>
                            </ul>
                </form>
                <h5>{I18n.t(`email_validation.sent.foot_a`)}</h5>
                <h5>{I18n.t(`email_validation.sent.foot_b`)}</h5>
          </div>
        </div>
        <Group className="homeOptions">
          <Group>
            <a  onClick={this.handleClick} className="btn fill success">{I18n.t(`email_validation.sent.button`)}</a>
          </Group>
        </Group>
        {
                this.state.isLoading ? (
                  <Loading><code>{I18n.t(`dialog_login.loading`)}</code></Loading>
                ) : null
        }
        {
          this.state.isEmailSent ? (
            <Dialog
              className={`dialog`}
              >
              <div className="inner" key={'pin'}>
                <div className="content">
                  <h5>{I18n.t(`email_validation.sent.dialog_title`)}: "{this.state.mail}"</h5>
                  <p>{I18n.t(`email_validation.sent.dialog_body`)}</p>
                </div>
                <Group>
                  <Group>
                    {/* eslint-disable-next-line */}
                    <a onClick={this.closeEmailSent} className="btn">{I18n.t(`email_validation.sent.dialog_button`)}</a>
                  </Group>
                </Group>
              </div>
            </Dialog>
          ): null
        } 
      </div>
    )
  }
}

export default EmailSent
